@import url(https://fonts.googleapis.com/css2?family=STIX+Two+Text:wght@400;600&display=swap);
*,*::after,*::before{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}body{box-sizing:border-box;font-family:"STIX Two Text",serif;scroll-behavior:smooth}h1{font-size:6rem;font-family:"STIX Two Text",serif}h2{font-size:4rem;margin:0}h3{font-size:3rem;margin:0}h4{font-size:2.5rem}a{font-size:1.8rem}hr{width:80%}p{font-size:1.8rem}
#home-page-container{padding:0 2%;width:100%}#home-description-container{height:100vh;width:100%;margin:0 auto;background-image:url(/portfolio/static/media/background.e88cac6e.png);background-position:center}#home-aboutme-container{height:80vh;width:100%;background-color:#e9e7e7;position:relative}#home-project-container{height:100%;width:100%;margin:0 auto;padding:2% 10%;background-color:#8d99ae}#footer-container{padding:1% 5%;height:100%;width:100%;display:flex;align-items:center;justify-content:center}
#heading-container{display:flex;flex-direction:column;align-items:center;justify-content:center;height:100%}#heading-text{color:#e9e7e7;text-align:center}#heading-name{color:#ef233c}
#aboutMe-content-container{height:100%;padding:10% 15%}#aboutMe-heading{margin:1.5rem 0}#aboutMe-picture{height:20rem;margin-right:3rem;margin-bottom:2rem;float:left}#portfolio-pic{max-height:100%;border-radius:2rem;margin:0}#content{margin-bottom:2rem}
#projects-heading{color:#e9e7e7;margin-bottom:3%}#projects-container{height:95%;width:100%;padding:3%;margin:2% 0;display:flex;flex-direction:column;align-items:center}#project{height:100%;margin-bottom:4%;padding:2% 10%}#image-container{height:18rem;margin-right:3rem;margin-bottom:3rem;float:left}#project-image{height:100%;width:100%;border:4px solid #edf2f4;transition:-webkit-transform .2s ease;transition:transform .2s ease;transition:transform .2s ease, -webkit-transform .2s ease}#project-image:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}#project-heading{margin-bottom:1.5rem}
#link-component-container{display:flex;justify-content:space-around}
#links-container{margin-bottom:1rem;display:flex;flex-direction:column;align-items:center;justify-content:space-around}#link-text{color:#284b63;text-decoration:none}#link-text:hover{-webkit-transform:scale(1.2);transform:scale(1.2)}#link-text:visited{color:#ef233c}@media only screen and (max-width: 360px){#links-container{margin-bottom:2rem}}
